import * as React from "react"
// Import ImageDepthMap
import ImageDepthMap from 'react-depth-map'
// Import Utilities
import { encodedWord, revealText } from '../utilities/revealText'



const BigPhoto = () => {



    // REF
    const textToReveal = React.useRef()



    // STATE
    const [ title ] = React.useState('TAN NATURAL QUE hicimos UN LOUNGE EN EL LUGAR MÃS NATURAL DEL MUNDO')



    // USE EFFECT
    React.useEffect( () => {
        revealText(textToReveal)
    })



    return (
        <section className="bigPhoto">
            {/* <ImageDepthMap
                originalImg={'https://eccorona.s3.us-east-2.amazonaws.com/imgs/bg-beach.jpg'}
                depthImg={'https://eccorona.s3.us-east-2.amazonaws.com/imgs/bg-beach--shadow.jpg'}
                verticalThreshold={40}
                horizontalThreshold={25}
                className="bigPhoto__bg" /> */}
            <picture className="bigPhoto__bg">
                <img src="https://eccorona.s3.us-east-2.amazonaws.com/imgs/bg-beach.jpg" alt="" className="bigPhoto__bg__img" />
            </picture>
            <div className="bigPhoto__container">
                <div className="bigPhoto__box max450">
                    <h2 className="bigPhoto__title"
                        ref={ textToReveal }
                        dangerouslySetInnerHTML={ encodedWord(title) } />
                </div>
            </div>
        </section>
    )
}



export default BigPhoto